@import url(https://fonts.googleapis.com/css2?family=Poppins:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital@1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Sidebar{
    width: 200px;
    height: 600px;
    border:1px solid black;
    border-style: none double none none;
}


 .Sidebar ul{
    list-style: none;
}

.Sidebar ul li{
    margin-bottom: 15px;
}


.Dashboard{
    
    display: flex;
}


.Dashboard ul{
    list-style: none;
}

.Dashboard .Dashboard-right ul li{
    margin-top: 10px;
    margin-left: 175px;
    display: inline-block;
    

}

.Dashboard .heading-2{
    margin-right: 40px;

}


.Dashboard .today{
    margin-top: 40px;
    margin-left: 175px;
    
}

.Dashboard .today h1{

    text-align: left;
    margin-left: 50px;

}


.Dashboard .today-list ul li{
    margin-top: 30px;
    margin-left: 175px;
    display: inline-block;
}
.report{

    display: flex;
}

.report ul{
    list-style: none;
}
.topbar{
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
    font-family: 'Poppins', sans-serif;
}

.topbar .left{
    margin-left: 20px;
}


.topbar .right {
    margin-right: 10px;
}


.topbar .right ul li{
    display: inline-block;
    margin-right: 20px;
}



.roomlist{
    display: flex;
    
}

.roomlist ul{
    list-style: none;
}

.roomlist .roomlist-right{
    margin-left: 40px;

}


.room-number ul li{
    margin-right: 100px;
    margin-top: 50px;
    display: inline-block;
}


.roomdetail {
    display: flex;
}

.roomdetail  .roomdetail-right{
    margin-left: 40px;
}

.roomdetail  .roomdetail-right h2{
    font-family: 'Poppins', sans-serif;
}





.patients-information{
    display: flex;
}

.patients-in-the-room{
    margin-top: 20px;
    margin-left: 40px;
    width: 520px;
    height: 400px;
    border:2px solid rgb(12, 12, 15);
    border-style: double double double double;
}

.patients-in-the-room ul{
    list-style: none;
}

.patients-in-the-room ul li{
    width: 100px;
    height: 150px;
    border:2px solid rgb(12, 12, 15);
    border-style: double double double double;
    display: inline-block;
    margin-left: 80px;
    margin-top: 30px;
}


.patients-detail{
    margin-left: 00px;
    width: 600px;
    height: 600px;
    
}

.patients-detail ul{
    list-style: none;
}


.patients-detail ul li{
    margin-right: 50px;
    margin-bottom: 50px;
    display: inline-block;
}


.nodata {
    text-align: center;
}
